import BaseMenu from "@/components/menu"
import useWindowSize from "@/hooks/useWindowSize"
import UiStore from "@/store/ui"
import { Box } from "@mui/material"
import { PropsWithChildren, useEffect, useState } from "react"
import useStyles from "./styles"

const Layout = ({ children }: PropsWithChildren<any>) => {
    const { classes } = useStyles()
    const [width] = useWindowSize()
    const { setShowMenu, content } = UiStore()
    const [currentShow, setCurrentShow] = useState(true)

    useEffect(() => {
        setShowMenu(width >= 768)
    }, [width, setShowMenu])

    return (
        <Box className={classes.layout}>
            {content && (
                <>
                    <BaseMenu
                        handleShowSub={(sub: boolean) => {
                            setCurrentShow(sub)
                        }}
                    />
                    <Box className="children-container">
                        <Box className="children-wrapper">{children}</Box>
                        {currentShow && <Box className="layer" id="layer"></Box>}
                    </Box>
                </>
            )}
        </Box>
    )
}

export default Layout
