import { Box, Stack, Typography } from "@mui/material"
import { useEffect, useMemo, useState } from "react"
import { useTranslation } from "react-i18next"
import { useLocation } from "react-router-dom"
import ReactSlidy from "react-slidy"
import { v4 as uuidv4 } from "uuid"

import UiStore from "@/store/ui"
export interface PageChild {
    pathname: string
    text: string
    url: string | string[] | null
    type: "link" | "video" | "slides" | "submenu"
    children?: PageChild[]
}
const MonoPage = () => {
    const { content } = UiStore()
    const location = useLocation()
    const [loading, setLoading] = useState(true)
    const { t } = useTranslation()

    const pageContent = useMemo(() => {
        const path = location?.pathname?.split("/")
        if (!path || path.length < 2) return null

        // Trouver la page parent
        const currentParent = content?.pages?.find((item) => item.pathname === path[1])
        if (!currentParent) return null

        // Si nous n'avons que le parent dans l'URL
        if (path.length === 2) {
            const firstChild = currentParent.children[0]
            return firstChild?.url || null
        }

        // Trouver le sous-menu
        const findContent = (items: PageChild[], pathIndex: number): string | string[] | null => {
            const currentItem = items.find((item) => item.pathname === path[pathIndex])
            if (!currentItem) return null

            // Si c'est le dernier segment de l'URL ou si c'est un contenu final
            if (pathIndex === path.length - 1 || !currentItem.children?.length) {
                return currentItem.url
            }

            // Rcursivement chercher dans les enfants
            if (currentItem.children) {
                return findContent(currentItem.children, pathIndex + 1)
            }

            return null
        }

        return findContent(currentParent.children, 2)
    }, [location?.pathname, content?.pages])

    useEffect(() => {
        if (pageContent) {
            setLoading(true)
            setTimeout(() => {
                setLoading(false)
            }, 500)
        } else {
            setLoading(false)
        }
    }, [pageContent])

    const renderContent = () => {
        if (!pageContent || loading) return null

        if (Array.isArray(pageContent)) {
            return (
                <Stack
                    alignContent="center"
                    alignItems="center"
                    flexDirection="column"
                    height="100%"
                    justifyContent="center"
                >
                    <ReactSlidy>
                        {pageContent.map((item) => (
                            <img src={item} key={uuidv4()} alt="" />
                        ))}
                    </ReactSlidy>
                </Stack>
            )
        }

        return (
            <iframe
                id="my-iframe"
                src={pageContent}
                style={{ width: "100%", height: "100%", border: "none" }}
                title="Content"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
                referrerPolicy="strict-origin-when-cross-origin"
                allowFullScreen
            />
        )
    }

    return (
        <Box
            sx={{
                width: "100%",
                height: "100%",
            }}
        >
            {renderContent()}
            {loading && (
                <Stack
                    sx={{
                        width: "100%",
                        height: "100%",
                    }}
                    flexDirection="column"
                    justifyContent="center"
                >
                    <Typography
                        sx={{
                            color: "white",
                            textAlign: "center",
                        }}
                    >
                        {t("LOADING")}
                    </Typography>
                </Stack>
            )}
        </Box>
    )
}

export default MonoPage
