import env from "@/config/env"
import useContextMenu from "@/hooks/useContextMenu"
import Layout from "@/layout"
import { CookiesService } from "@/services"
import LangService from "@/services/lang"
import UiStore from "@/store/ui"
import axios from "axios"
import { useEffect } from "react"
import { useTranslation } from "react-i18next"
import { Outlet } from "react-router-dom"
const GlobalOutlet = () => {
    const { preferredLanguage, content, updateState } = UiStore()
    const { i18n, t } = useTranslation()
    useContextMenu()
    useEffect(() => {
        const fetchData = async () => {
            try {
                const SUPABASE_ANON_KEY =
                    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InFna3doYW9seWtneHVxbWdxcHBrIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzU3NDg4ODQsImV4cCI6MjA1MTMyNDg4NH0.ZYZKElLS8dudtQfpnqNh2ASePr-GrhE4qDglmHRfYTw"
                const response = await axios({
                    method: "GET",
                    url: `${env.API_BASE_URL}public_configurations?select=*`,
                    headers: {
                        apikey: SUPABASE_ANON_KEY,
                        Authorization: `Bearer ${SUPABASE_ANON_KEY}`,
                        "Content-Type": "application/json",
                    },
                })
                if (response.data.length) {
                    updateState({
                        content: {
                            site: JSON.parse(response.data?.[0]?.site ?? {}),
                            pages: JSON.parse(response.data?.[0]?.pages ?? {}),
                        },
                    })
                }
            } catch (error) {
                console.log("failed to fetch data", error)
            }
        }
        if (!content) {
            fetchData()
        }
    }, [])
    useEffect(() => {
        if (content) {
            document.title = content?.site?.sitename
        }
    }, [t, content])

    useEffect(() => {
        if (content?.site?.logo) {
            const link = document.querySelector("link#favicon")
            if (link) link.setAttribute("href", content?.site?.logo + "?v=2")
        }
    }, [content?.site?.logo])

    useEffect(() => {
        function handleLanguageChange() {
            const language = LangService.preloadLanguage(navigator.languages)
            updateState({ preferredLanguage: language })
        }
        window.addEventListener("languagechange", handleLanguageChange)
        return () => {
            window.removeEventListener("languagechange", handleLanguageChange)
        }
    }, [updateState])

    useEffect(() => {
        const language = LangService.preloadLanguage(navigator.languages)
        document.documentElement.lang = language
        CookiesService.set({ lang: language })
        updateState({ preferredLanguage: language })
    }, [updateState])

    useEffect(() => {
        i18n.changeLanguage(preferredLanguage)
    }, [preferredLanguage, i18n])

    return <Layout>{content && <Outlet />}</Layout>
}

export default GlobalOutlet
