import UiStore from "@/store/ui"
import { Box, Button, Stack, Typography } from "@mui/material"
import { useCallback, useEffect, useMemo, useRef, useState } from "react"
import { useTranslation } from "react-i18next"
import { Link, useLocation, useNavigate } from "react-router-dom"
import { v4 as uuidv4 } from "uuid"
import Icon from "../icons"
import useStyles from "./styles"

const BaseMenu = ({ handleShowSub }) => {
    const { classes } = useStyles()
    const location = useLocation()
    const navigate = useNavigate()
    const [openMenu, setOpenMenu] = useState(true)
    const { t } = useTranslation()
    const [showSub, setShowSub] = useState(true)
    const { content } = UiStore()
    const menuRef = useRef(null)
    const parentMenuRef = useRef(null)

    const handleClickOutside = (event) => {
        if (event.target.id === "layer") {
            setShowSub(false)
        }
    }

    useEffect(() => {
        if (showSub) {
            document.addEventListener("mousedown", handleClickOutside)
        } else {
            document.removeEventListener("mousedown", handleClickOutside)
        }

        return () => {
            document.removeEventListener("mousedown", handleClickOutside)
        }
    }, [showSub])

    const isActiveMenu = useCallback(
        (pathname) => {
            const path = location?.pathname?.split("/")
            return path[1] === pathname
        },
        [location?.pathname]
    )

    const isActiveSubMenu = useCallback(
        (childPath, parentPath, level = 2) => {
            const path = location?.pathname?.split("/")
            return path[1] === parentPath && path[level] === childPath
        },
        [location?.pathname]
    )

    const makePath = useCallback((item) => {
        // Si l'item n'a pas d'enfants, retourner juste son pathname
        if (!item?.children?.length) {
            return item?.pathname
        }

        const firstChild = item?.children[0]

        // Si ce n'est pas un sous-menu ou pas d'enfants, retourner le chemin simple
        if (!firstChild) {
            return item?.pathname
        }

        // Pour les sous-menus, construire le chemin complet
        if (item?.type === "submenu" && firstChild?.children?.length) {
            const firstGrandChild = firstChild?.children[0]
            return `${item?.pathname}/${firstChild?.pathname}/${firstGrandChild?.pathname}`
        }

        // Pour les autres types, retourner le chemin avec l'enfant direct
        return `${item?.pathname}/${firstChild?.pathname}`
    }, [])

    const currentPage = useMemo(() => {
        const path = location?.pathname?.split("/")
        const currentParent = content?.pages?.find((item) => {
            return item?.pathname === path[1]
        })

        return currentParent || null
    }, [location?.pathname, content?.pages])

    useEffect(() => {
        setShowSub(true)
        if (location?.pathname === "/" && content) {
            const pages = content?.pages
            if (pages?.length) {
                const firstPage = pages[1]
                if (firstPage?.children?.length) {
                    navigate(makePath(firstPage))
                }
            }
        }
    }, [location, content, navigate, makePath])

    useEffect(() => {
        handleShowSub(showSub)
    }, [showSub, handleShowSub])

    const renderSubMenu = (children, parentPath = "", level = 1) => {
        if (level > 2) return null

        return children?.map((child) => (
            <Box component={"li"} key={uuidv4()} className={`child-menu menu-item`}>
                {child?.type === "submenu" && level === 1 ? (
                    <>
                        <Typography
                            className={`path withchildren ${
                                isActiveSubMenu(child?.pathname, currentPage?.pathname) ? "active" : ""
                            }`}
                        >
                            <Typography component={"span"}>{child?.text}</Typography>
                        </Typography>
                        {child?.children && (
                            <Box component={"ul"} className="ul-children">
                                {renderSubMenu(child.children, `${parentPath}/${child.pathname}`, level + 1)}
                            </Box>
                        )}
                    </>
                ) : (
                    <Link
                        to={
                            level === 1
                                ? `${currentPage?.pathname}/${child?.pathname}`
                                : `${currentPage?.pathname}${parentPath}/${child?.pathname}`
                        }
                        className={`path single ${
                            isActiveSubMenu(child?.pathname, currentPage?.pathname, level + 1) ? "active" : ""
                        }`}
                    >
                        <Typography component={"span"}>{child?.text}</Typography>
                    </Link>
                )}
            </Box>
        ))
    }

    return (
        <Box
            sx={{
                height: "100%",
                position: "relative",
                overflow: "scroll",
                scrollbarWidth: "none",
                "-ms-overflow-style": "none",
                "&::-webkit-scrollbar": {
                    display: "none",
                },
            }}
        >
            <Button
                sx={{
                    background: "#FFFCF0",
                    borderRadius: "1px",
                    position: "fixed",
                    zIndex: 100,
                    "&:hover": {
                        background: "#3B2D9B",
                        "& svg path": {
                            color: "#fff",
                            fill: "#fff",
                            stroke: "#fff",
                        },
                    },
                    "&.isMenuOpen": {
                        position: "static",
                        width: "132px",
                        padding: ".5rem",
                        "@media(max-width: 550px)": {
                            width: "116px",
                        },
                    },
                }}
                onClick={() => {
                    setOpenMenu(!openMenu)
                }}
                className={openMenu ? "isMenuOpen" : ""}
            >
                <Icon name={"burger"} />
            </Button>
            <Stack
                sx={{
                    background: "#FFFCF0",
                    overflowY: "scroll",
                    padding: ".5rem",
                    transition: "all ease-in .3s",
                }}
                className={`${classes["menu-layout"]} ${openMenu ? "openMenu" : ""}`}
                ref={parentMenuRef}
            >
                <Stack>
                    <img src={`${content?.site?.logo}`} alt={content?.site?.sitename} />
                </Stack>
                <Stack>
                    <Box component={"ul"} className="menu-items">
                        {content?.pages?.map((item) => (
                            <Box
                                component={"li"}
                                key={uuidv4()}
                                className={`menu-item ${isActiveMenu(item?.pathname) ? "active" : ""} `}
                            >
                                <Link to={`${makePath(item)}`} className="path parent-item">
                                    <Stack
                                        flexDirection={"column"}
                                        alignContent={"center"}
                                        alignItems={"center"}
                                        gap={1}
                                    >
                                        <Icon
                                            name={`${item?.icon}${isActiveMenu(item?.pathname) ? "-active" : ""}`}
                                            sx={{
                                                width: "30px",
                                                height: "30px",
                                            }}
                                            className="icon-menu"
                                        />
                                        <Typography
                                            sx={{
                                                textAlign: "center",
                                            }}
                                            className="item-menu"
                                        >
                                            {t(item?.menuname)}
                                        </Typography>
                                    </Stack>
                                </Link>
                            </Box>
                        ))}
                    </Box>
                </Stack>
                {currentPage?.type !== "slides" && currentPage?.children?.length > 1 && (
                    <Stack
                        justifyContent={"flex-end"}
                        width={"100%"}
                        flexDirection={"row"}
                        className={`${classes["button-stack"]} ${!showSub ? "slide" : ""} ${
                            !openMenu ? "hideStack" : ""
                        }`}
                    >
                        <Button
                            sx={{
                                color: "transparent",
                                textAlign: "right",
                                minWidth: "fit-content",
                                padding: ".1rem",
                                height: "fit-content",
                                "& svg": {
                                    width: "1em",
                                    height: "1em",
                                },
                            }}
                            onClick={() => {
                                setShowSub(!showSub)
                            }}
                        >
                            <Icon name={showSub ? "chevron-left" : "chevron-right"} />
                        </Button>
                    </Stack>
                )}
                {showSub && <Stack className={classes["layer"]}></Stack>}
                {currentPage?.type !== "slides" && currentPage?.children?.length > 1 && (
                    <Stack
                        ref={menuRef}
                        className={`${classes["child-layout"]} ${showSub ? "open" : "closed"} ${
                            !openMenu ? "hideStack" : ""
                        }`}
                    >
                        <Box component={"ul"} className="menu-items">
                            {renderSubMenu(currentPage?.children)}
                        </Box>
                    </Stack>
                )}
            </Stack>
        </Box>
    )
}

export default BaseMenu
